    p(data-aos="fade") Durante los ltimos aos, la computacin en la nube ha permitido que las empresas logren tener presencia corporativa en la #[em web], y que la adquisicin de recursos tecnolgicos informticos sea una inversin econmica muy cmoda o razonable a manera de pago por servicios. 
    p.mb-4(data-aos="fade") Ahora no es necesario adquirir equipos o infraestructura fsica para suplir necesidades de organizacin, administracin o mantenimiento. A continuacin, se detallan las diferentes tecnologas de computacin en la nube:

