    p.mb-5(data-aos="fade") Cuando hablamos de computacin en la nube, se mencionan una serie de componentes que hacen parte del conjunto, dentro de los cuales se destacan:

    #1.subtitulo.mb-4(data-aos="fade")
      h3.mb-0 Redes de datos

    .row.align-items-end.mb-4
      .col-lg-4.col-md-7.col-10.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/imagredes.svg', alt='')
      .col-lg-8(data-aos="fade-left")
        p Las redes de datos tienen por funcin potenciar e incrementar la productividad de las organizaciones al permitir que todos los dispositivos que se encuentran conectados puedan acceder a los recursos que se encuentran compartidos, en los ltimos aos la tecnologa ha entrado a jugar un papel preponderante en el desarrollo y evolucin de las organizaciones y cada vez est ms inmersa en todos los procesos y actividades empresariales, personales y cotidianas.
        .cajon.color-secundario.bg-9.p-4.mb-4
          p.mb-0 Las redes han permitido que los servicios y el acceso a estos recursos sea cada vez ms necesarios, ya que se requiere tener en todo momento trabajo en equipo, colaborativo y la integracin entre los diferentes recursos tecnolgicos, esto es lo que conocemos como el #[em networking]. Para que dichas redes pueden interconectarse y trabajar en red se requiere de una serie de componentes desde el punto de vista fsico y lgico, dentro de los cuales destacamos los siguientes:
    
    .row.mb-5(data-aos="fade-down")
