    #2.subtitulo.mb-4(data-aos="fade")
      h3.mb-0 Modelo de referencia
    
    .row.align-items-center.mb-4
      .col-lg-2.col-md-3.col-7.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/imagref.svg', alt='')
      .col-lg-10.col-md-9(data-aos="fade-left")
        p Al inicio del desarrollo de la tecnologa y ms propiamente de la computacin, los diferentes fabricantes construan sus equipos y dispositivos para que fueran compatibles con dispositivos de sus propias marcas lo que no facilitaba la interconexin de equipos y dispositivos de fabricantes distintos.
    
    p(data-aos="fade") Esto llev a la necesidad de estandarizar de manera que se permita la interconexin de una amplia gama de dispositivos sin importar el fabricante o tipo de #[em software]. Para que el intercambio de informacin sea posible se requiere el establecimiento de una serie de estndares.
    p.mb-4(data-aos="fade") Estos estndares son construidos por una serie de organizaciones que son las siguientes:
    
    .row.align-items-center.mb-5
      .col-lg-7.col-12.mb-5.mb-md-0(data-aos="fade-right")
        LineaTiempoD.color-acento-botones
          p(numero="1" titulo="ISO (Organizacin Internacional de Normas)") ISO defini un modelo de referencia como estndar para posibilitar las comunicaciones electrnicas a nivel global, conocido como el modelo #[em OSI] (Interconexin de Sistemas Abiertos).
          p(numero="2" titulo="IEEE (Instituto de Ingenieros Electrnicos y Elctricos)") Este organismo es encargado de fijar los estndares para los elementos fsicos de una red, cables, conectores e interfaces.
          p(numero="3" titulo="ANSI (Instituto Nacional Americano de Normalizacin)") Es una organizacin sin fines de lucro que supervisa el desarrollo de estndares para productos, servicios, procesos y sistemas en los Estados Unidos.        
          p(numero="4" titulo="EIA (Asociacin de Industrias Electrnicas)") Es una organizacin formada por la asociacin de las compaas electrnicas y de alta tecnologa de los Estados Unidos, cuya misin es promover el desarrollo de mercado y la competitividad de la industria de alta tecnologa de los Estados Unidos con esfuerzos locales e internacionales de la poltica.
          p(numero="5" titulo="TIA (Asociacin de Industrias Telecomunicaciones)") Es una asociacin comercial acreditada por el Instituto Nacional Estadounidense de Estndares (ANSI), con el fin de desarrollar normas industriales, tanto voluntarias como basadas en el consenso, sobre una amplia variedad de productos de las tecnologas de la informacin y la comunicacin (TIC), y actualmente representa a casi 400 empresas. El Departamento de Estndares y Tecnologa de TIA opera doce comits de ingeniera, que desarrollan pautas para equipos de radio privados, antenas celulares, terminales de datos, satlites, equipos de terminales telefnicos, accesibilidad, dispositivos VoIP, cableado estructurado, centros de procesamiento de datos, comunicaciones de dispositivos mviles, multidifusin multimedia, telemtica vehicular, TIC utilizadas en el cuidado de la salud, comunicaciones M2M y redes de servicios inteligentes.        
          p(numero="6" titulo="UIT (Unin Internacional de Telecomunicaciones)") La Unin Internacional de Telecomunicaciones es el organismo especializado en telecomunicaciones de la Organizacin de las Naciones Unidas, encargado de regular las telecomunicaciones a nivel internacional entre las distintas administraciones y empresas operadoras. Su sede se encuentra en la ciudad de Ginebra.
      .col-lg-4.d-none.d-lg-flex(data-aos="fade-left")
        img(src='@/assets/curso/temas/imaglinea.png', alt='')

    
    .row.justify-content-center.t1-1.mb-5(data-aos="fade-down")
