            p.mb-0 Los modelos de referencia se basan en la definicin de una serie de capas las cuales tienen sus propias funciones y caractersticas que se encargan de resolver un problema diferente en la comunicacin, tienen como propsito asistir en la comprensin ms clara y sencilla de las funciones y los procesos involucrados en la interconexin de los diferentes dispositivos de red, para ello se establecen una serie de capas con un orden especfico y cada una se sustenta en la capa anterior, y ms que entrar en los detalles de las especificaciones tcnicas requeridas en cada una de ellas, el modelo de referencia ayuda en la comprensin de lo que sucede, ayudndonos a lograr un mejor entendimiento de las funciones y procesos involucrados.

    .container.px-4.pt-4.fondo-1.mb-4(data-aos="fade-down")
      p.mb-4 El modelo de referencia para las redes de comunicaciones es el modelo OSI - Interconexin de Sistemas Abiertos, el cual se explica a continuacin:
