    p.mb-4(data-aos="fade") Para que los paquetes de datos puedan viajar desde el origen hasta su destino, cada capa del modelo #[b OSI] en el origen debe comunicarse con su capa igual en el lugar destino. Esta forma de comunicacin se conoce como comunicaciones de par-a-par, y en cada una de estas capas la informacin cambia de nombre, es decir que usan un PDU (#[em Protocol Data Unit]) diferente.

