                td Enlace de Datos
                td Datos
              tr
                td 1
                td Fsica
                td Datos

    p.mb-5 El modelo OSI es el modelo de referencia de internetwork ms conocido. Es utilizado para el diseo de redes de datos, especificaciones de funcionamiento y la resolucin de problemas ya que permite ir descartando en cada capa el posible problema.
    
    #3.subtitulo.mb-4(data-aos="fade")
      h3.mb-0 Modelo de referencia TCP/IP

    .row.align-items-center.mb-4
      .col-lg-4.col-md-6.col-10.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/imag04.svg', alt='')
      .col-lg-8(data-aos="fade-left")
        p Fue el primer modelo de protocolo en capas para comunicaciones de red. Fue desarrollado a principios de la dcada de los 70 por el departamento de defensa de los EE. UU., y se conoce con el nombre de modelo de Internet, su objetivo fue el de crear una red de comunicaciones que funcionara incluso en condiciones de guerra.
        .cajon.color-secundario.bg-9.p-4.mb-4
          p Aunque el modelo OSI es universalmente reconocido, el estndar histrica y tcnicamente reconocido para Internet es el modelo TCP/IP (Protocolo de Control de Transferencia / Protocolo de Internet).
          p.mb-0 Define cuatro categoras de funciones que deben existir para que las comunicaciones sean exitosas. La arquitectura de la #[em suite] de protocolos TCP/IP sigue la estructura de este modelo. Por esto, es comn que al modelo de Internet se le conozca como modelo TCP/IP.

    p.mb-4(data-aos="fade") El modelo consta de cuatro capas, como se presenta en la figura a continuacin:

    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-6.col-md-7.col-11
        .titulo-sexto.color-acento-contenido
          h5 Figura 1.
          span  Modelo de referencia TCP/IP
        img.mb-3.d-lg-flex.d-none(src="@/assets/curso/temas/imag05.svg" alt="El diagrama describe el modelo TCP/IP y sus 4 capas de la siguiente manera: Capa 4, aplicacin, representa datos para el usuario ms el control de codificacin y de dilogo. Capa 3, transporte, admite la comunicacin entre distintos dispositivos de distintas redes. Capa 2, internet, determina la mejor ruta a travs de la red. Capa 1, acceso a la red, controla los dispositivos de hardware y los medios que forman la red.")
        img.mb-3.d-flex.d-lg-none(src="@/assets/curso/temas/t1/fig1-mob.svg" alt="El diagrama describe el modelo TCP/IP y sus 4 capas de la siguiente manera: Capa 4, aplicacin, representa datos para el usuario ms el control de codificacin y de dilogo. Capa 3, transporte, admite la comunicacin entre distintos dispositivos de distintas redes. Capa 2, internet, determina la mejor ruta a travs de la red. Capa 1, acceso a la red, controla los dispositivos de hardware y los medios que forman la red.")
        figcaption 
          p.mb-0 #[b Fuente.] Adaptado de 
            a(:href="('http://cidecame.uaeh.edu.mx/lcc/mapa/PROYECTO/libro35/tcp.png')" target="_blank")
              span http://cidecame.uaeh.edu.mx/lcc/mapa/PROYECTO/libro35/tcp.png

    
    #4.subtitulo.mb-4(data-aos="fade")
      h3.mb-0 Protocolos de comunicacin
    
    p.mb-4(data-aos="fade") Las comunicaciones en general requieren de una serie de elementos y actores que intervienen dentro de los cuales podemos encontrar:

    .row.justify-content-center.align-items-center
      .col-lg-4.d-lg-flex.d-none(data-aos="fade-right")
        img.mb-4(src="@/assets/curso/temas/imag08.svg")
      .col-10.col-lg-8.col-md-10(data-aos="fade-left")
        .row.justify-content-center.mb-5
          .col-lg-7
            SlyderF.t1-1
              .tarjeta.p-4.borde-gris
                .row.justify-content-center
                  .col-6.col-md-5.col-lg-4.mb-4.mb-md-3
                    img(src='@/assets/curso/temas/icono08.svg' alt='').mx-auto        
                  .col-md-10
                    .text-center
                      h3.mb-2 Emisor
                      p Origen del mensaje (personas y/o dispositivos).
              .tarjeta.p-4.borde-gris
                .row.justify-content-center
                  .col-6.col-md-5.col-lg-4.mb-4.mb-md-3
                    img(src='@/assets/curso/temas/icono09.svg' alt='').mx-auto         
                  .col-md-10
                    .text-center
                      h3.mb-2 Destino
                      p Receptor del mensaje (personas y/o dispositivos).
              .tarjeta.p-4.borde-gris
                .row.justify-content-center
                  .col-6.col-md-5.col-lg-4.mb-4.mb-md-3
                    img(src='@/assets/curso/temas/icono10.svg' alt='').mx-auto          
                  .col-md-10
                    .text-center
                      h3.mb-2 Canal
                      p Medios por los que el mensaje viaja.
              .tarjeta.p-4.borde-gris
                .row.justify-content-center
                  .col-6.col-md-5.col-lg-4.mb-4.mb-md-3
                    img(src='@/assets/curso/temas/icono11.svg' alt='').mx-auto        
                  .col-md-10
                    .text-center
                      h3.mb-2 Cdigo
                      p Lenguaje utilizado para formar el mensaje.
              .tarjeta.p-4.borde-gris
                .row.justify-content-center
                  .col-6.col-md-5.col-lg-4.mb-4.mb-md-3
                    img(src='@/assets/curso/temas/icono12.svg' alt='').mx-auto         
                  .col-md-10
                    .text-center
                      h3.mb-2 Mensaje
                      p Elemento a transmitir. 
        .cajon.color-secundario.p-4.mb-4
          p.mb-0 Todas las comunicaciones deben contar con un emisor, un mensaje a enviar y un cdigo que permite que el mensaje viaje por un canal. Para que ambos actores puedan interactuar requieren contar con un cdigo o lenguaje comn, que permita que puedan entenderse, ese estndar o lenguaje comn es lo que conocemos como un protocolo de comunicaciones, el cual es un conjunto de normas que estn obligadas a cumplir todos los dispositivos #[em hardware] y #[em software] que intervienen en una comunicacin de datos entre computadoras y dems dispositivos, sin estas reglas o estndares la comunicacin resultara catica y por tanto imposible.


    p.mb-4(data-aos="fade") Dependiendo de los servicios a los cuales accedemos a travs de las redes de computadores ser el protocolo que se deber emplear en la comunicacin entre el usuario que solicita y accede al servicio y el dispositivo quien se lo entrega, en general en la transmisin y recepcin de los datos podemos encontrar dos grandes familias de protocolos que se resumen en:
    
    TabsB.color-acento-botones.mb-4(data-aos="fade-down")
      .bg8.py-4.py-md-5(titulo="Protocolos orientados a la conexin" :icono="require('@/assets/curso/temas/icono13.svg')")
        .row
          .col-md-6.mb-4.mb-md-0
            p Estos requieren un modo de comunicacin de redes donde se debe establecer una conexin antes de transferir datos. Se identifica el flujo de trfico con un identificador de conexin en lugar de utilizar explcitamente las direcciones de la fuente y el destino.
            p.mb-4 Se dice que un servicio de comunicacin entre dos dispositivos es orientado a conexin cuando antes de iniciar la comunicacin se valida el cumplimiento de ciertas variables como (disponibilidad, alcance, origen, destino, etc.) entre estos dispositivos y se negocian unas credenciales para hacer esta conexin ms segura y eficiente.
          .col-md-6
            .titulo-sexto.color-acento-contenido
              p.mb-0 #[b Figura 2.] Servicios orientados a conexin 
            figure
              img(src='@/assets/curso/temas/imag09.svg', alt='Se presenta un diagrama donde un equipo A(emisor), establece una conexin con el equipo B(receptor) a travs del paquete 1 que lleva el mensaje, y el paquete B que asegura su recepcin en el destino.')
      .bg8.py-4.py-md-5(titulo="Protocolos no orientados a la conexin" :icono="require('@/assets/curso/temas/icono14.svg')")
        .row
          .col-md-6.mb-4.mb-md-0
            p Sucede cuando una comunicacin entre dos puntos finales de una red en los que un mensaje puede ser enviado desde un punto inicial o de origen a otro punto o dispositivo de la red sin acuerdo previo. El dispositivo en un extremo de la comunicacin transmite los datos al otro, sin tener que asegurarse de que el receptor est disponible y listo para recibir los datos. El emisor simplemente enva un mensaje dirigido al receptor.
          .col-md-6
            .titulo-sexto.color-acento-contenido
              p.mb-0 #[b Figura 3.] Servicios de red no orientados a conexin 
            figure
              img(src='@/assets/curso/temas/imag10.svg', alt='Se presenta un diagrama donde el equipo A (emisor) enva un mensaje a travs de dos paquetes 1 y 2, sin asegurar su llegada al equipo B (receptor)')

    p.mb-4(data-aos="fade") En la figura que se encuentra a continuacin, se evidencian algunos ejemplos de los protocolos que son usados en los servicios que normalmente utilizamos en la comunicacin entre las redes de datos.
    
    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-6.col-md-10
        .titulo-sexto.color-acento-contenido
          h5 Figura 4.
          span Ejemplos de protocolos
        figure
          img.mb-3(src="@/assets/curso/temas/tabla.svg" alt="La figura presenta una comparacin entre el modelo OSI y el modelo TCP/IP, mencionando tambin los diferentes conjuntos de protocolos utilizados en sus funciones y que son compartidos por ambos modelos.")
        figcaption 
          p.mb-0 #[b Fuente.] Adaptado de 
              a(:href="('https://interpolados.files.wordpress.com/2017/03/17.png')" target="_blank")
                span https://interpolados.files.wordpress.com/2017/03/17.png
          //p.mb-0 #[b Fuente.] https://interpolados.files.wordpress.com/2017/03/17.png 


    #5.subtitulo.mb-4(data-aos="fade")
      h3.mb-0 Puertos de comunicacin

    p.mb-4(data-aos="fade") Un puerto de comunicaciones es una interfaz a travs de la cual los diferentes dispositivos que intervienen en una comunicacin de red pueden enviar y recibir diferentes tipos de datos, los puertos pueden ser de tipo fsico (#[em hardware]) o lgicos (#[em software]), a continuacin, se ofrece una breve explicacin al respecto:
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
        .col-lg-9.col-xl-9(data-aos="fade")
          .titulo-sexto.color-acento-contenido
            h5 Figura 5. 
            span Puertos de comunicaciones
          ImagenInfografica.color-secundario
            template(v-slot:imagen)
              figure
                img(src="@/assets/curso/temas/imag12.svg" alt="La figura presenta un diagrama de flujo que conecta de manera secuencial los siguientes conceptos: solicitud de intercambio de texto, Servicio SMTP (protocolo TCP), puerto lgico (25), puerto fsico (RJ-45), red local (LAN)")
            
            .tarjeta.tarjeta-azul(x="59.5%" y="65.5%" numero="")
              .head_tar.mt-0.p-2
              .p-3
                h4.text2.mb-3 Puertos lgicos
                p.text3.mb-4 Los puertos lgicos son aquellos que utilizan un espacio de la memoria principal del dispositivo (RAM) que se asocia con un puerto fsico o un canal de comunicacin, y que proporciona un espacio para el almacenamiento temporal de la informacin que se va a transferir entre la localizacin de memoria y el canal de comunicacin.
                figure
                  img(src='@/assets/curso/temas/imag13.svg' alt='Se presenta un ejemplo de los puertos lgicos que son segmentos de la memoria principal (RAM) de un dispositivo que estn asociados con puertos fsicos o canales de comunicacin. Su funcin es proporcionar un espacio temporal para el almacenamiento de la informacin que se transferir entre la memoria del dispositivo y el canal de comunicacin. Estos puertos permiten que los datos se gestionen de manera eficiente, facilitando la comunicacin entre diferentes componentes del sistema o con dispositivos externos.')
            .tarjeta.tarjeta-azul(x="90.5%" y="65.5%" numero="")
              .head_tar.mt-0.p-2
              .p-3
                h4.text2.mb-3 Puertos fsicos
                p.text3.mb-4 Los puertos fsicos permiten la conexin de diferentes componentes tanto internos como externos los que llamamos comnmente perifricos. 
                figure
                  img(src='@/assets/curso/temas/imag14.svg' alt='Se presenta un ejemplo de los puertos fsicos que son interfaces que permiten la conexin de diversos componentes, tanto internos como externos, incluyendo los perifricos. Estos puertos facilitan la interaccin del dispositivo con otros equipos o accesorios, permitiendo el intercambio de datos y la comunicacin eficiente entre ellos.')
    
    p.mb-5(data-aos="fade") Para que se establezca una comunicacin entre un origen y un destino se requiere entonces que tanto el emisor como el receptor utilicen el mismo protocolo de comunicaciones, pero adems se requiere de un puerto de comunicacin que facilite que los datos enviados desde el origen vayan por un canal exclusivo.


    #6.subtitulo.mb-4(data-aos="fade")
      h3.mb-0 Medios de transmisin
    
    .row.align-items-center.mb-4
      .col-lg-4.col-md-6.col-10.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/imag15.svg', alt='')
      .col-lg-8(data-aos="fade-left")
        p Para que la comunicacin entre un emisor y un receptor se realice se requiere de un medio que facilite la conexin entre ambos extremos el cual es conocido como el medio de transmisin, este constituye el soporte fsico a travs del cual emisor y receptor pueden comunicarse en un sistema de transmisin de datos. Los medios de transmisin se pueden categorizar en dos grandes tipos, que son los medios guiados y no guiados.
        .cajon.color-secundario.bg-9.p-4.mb-4
          p.mb-4 Los medios guiados conducen (guan) las seales a travs de un camino fsico, tal como se muestra en la figura 6, dentro de los cuales podemos encontrar:

