    p(data-aos="fade") Los medios de transmisin no guiados son aquellos sin cable, transportan ondas electromagnticas sin usar un conductor fsico, sino que se radian a travs del aire, por lo que estn disponibles para cualquiera que tenga un dispositivo capaz de aceptarlas.
    p(data-aos="fade") Normalmente utilizan el aire como el medio a travs del cual viajan las seales en frecuencias electromagnticas, por lo que generalmente requieren de una antena que permita transmitir y recibir datos de diferentes tipos.
