    p.mb-5(data-aos="fade") Antes de la llegada de la computacin en la nube, los servicios, infraestructura, plataformas y en general los recursos de TI deban ser instalados, configurados y administrados en su totalidad por la empresa lo que conlleva varios inconvenientes entre los cuales se pueden mencionar los siguientes:

