          .col
            p El almacenamiento de los datos se hace de manera local en dispositivos como discos duros y servidores locales, este trae consigo el problema de que, si llegase a fallar el servidor, se corre el riesgo de la prdida de informacin o prdida de acceso a los servicios.
      .crd.crd--avatarHorizontal.py-4
        .row.align-items-center
          .col-auto
            img(src="@/assets/curso/temas/t1/tarj-conect2.svg", alt="").img100
          .col
            p La administracin de los recursos, que se relaciona con: el suministro de energa, aire acondicionado, sistemas de proteccin UPS y el mantenimiento preventivo y/o correctivo de los dispositivos, sin mencionar el hecho de que la tecnologa en cuanto a infraestructura, va perdiendo su valor y se va devaluando rpidamente debido al constante desarrollo y evolucin.
      .crd.crd--avatarHorizontal.crd--third.py-4
        .row.align-items-center
