            img(src="@/assets/curso/temas/t1/tarj-conect3.svg", alt="").img100
          .col
            p A nivel de aplicaciones se tiene el inconveniente de tener que administrarse no solo la aplicacin sino la plataforma de desarrollo, adicionalmente si se cuenta con aplicaciones no web sino tipo escritorio se obliga a tener que estar actualizando el servidor y los clientes que acceden a estas, una razn ms para disponer de aplicaciones como servicio ofrecidas a travs de la nube.
      .crd.crd--avatarHorizontal.crd--last.py-4
        .row.align-items-center
          .col-auto
            img(src="@/assets/curso/temas/t1/tarj-conect4.svg", alt="").img100
          .col
            p A nivel de conectividad en la mayora de las ocasiones el acceso a los servicios, aplicaciones y recursos normalmente est restringido a un acceso local, puesto que disponer de estrategias de conexin remotas requiere tener adicionalmente dispositivos de proteccin perimetral como #[em firewalls] y requerir un nivel adicional de proteccin y administracin desde el punto de vista de la ciberseguridad.

    //.row.align-items-center.mb-5
