        LineaTiempoD.color-acento-botones
          p(numero="1" titulo="Paso 1") El almacenamiento de los datos se hace de manera local en dispositivos como discos duros y servidores locales, este trae consigo el problema de que si llegase a fallar el servidor se corre el riesgo de la prdida de informacin o prdida de acceso a los servicios.
          p(numero="2" titulo="Paso 2") La administracin de los recursos, que se relaciona con: el suministro de energa, aire acondicionado, sistemas de proteccin UPS y el mantenimiento preventivo y/o correctivo de los dispositivos, sin mencionar el hecho de que la tecnologa en cuanto a infraestructura, va perdiendo su valor y se va devaluando rpidamente debido al constante desarrollo y evolucin.
          p(numero="3" titulo="Paso 3") A nivel de aplicaciones se tiene el inconveniente de tener que administrarse no solo la aplicacin si no la plataforma de desarrollo, adicionalmente si se cuenta con aplicaciones no web sino tipo escritorio se obliga a tener que estar actualizando el servidor y los clientes que acceden a estas, una razn ms para disponer de aplicaciones como servicio ofrecidas a travs de la nube.        
