    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      .h2 1.3 Evolucin de la informtica en la nube
    
    .row.align-items-end.mb-4
      .col-lg-4.col-md-6.col-10.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/compu.svg', alt='')
      .col-lg-8(data-aos="fade-left")
        p Si bien la computacin en la nube es un trmino que se populariz como un nuevo paradigma en el acceso a servicios e infraestructura en los aos 2008 y 2009, desde la dcada del 60 ya se hablaba de este paradigma y, es a John McCarthy a quien se le atribuye la definicin y el concepto tanto de cmputo en la nube como el de inteligencia artificial.
        .cajon.color-secundario.bg-9.p-4.mb-4
          p.mb-0 Posteriormente surgen nuevos autores como Joseph Carl Robnett Licklider, quien en sus conceptos siempre habl de contar con una tecnologa que permitiera que todo el mundo estuviera interconectado y que de esta manera  pudiera acceder a la informacin desde cualquier lugar sin lmites de distancias ni fronteras, este sueo dio origen a la computacin en la nube, la cual se crea con el propsito de poder descentralizar los recursos de cmputo y almacenamiento para distribuirlos alrededor del mundo y facilitar el acceso a la informacin cada vez a ms personas.
    
    .row.justify-content-center.t1-3.mb-5(data-aos="fade-down")
      .col-lg-10      
        .bloque-texto-g.color-secundario.p-4.p-sm-4
