            :style="{'background-image':`url(${require('@/assets/curso/temas/cielo.png')})`}")
          .bloque-texto-g__texto.p-4
            p.mb-0 Los orgenes del trmino nube, comnmente se asocian al trmino que se deriv de una metfora que tomaba como base al dibujo de nubes que se utilizaban en el pasado para representar a la red telefnica, y que posteriormente algunos comenzaron a representar al Internet.
    
    h5.mb-4(data-aos="fade") Podemos resumir la evolucin en las siguientes fases:
    
    .row.fondo-t1.align-items-center.py-4.mb-5
      .col-lg-12.col-12.px-lg-5.px-4
        .row.justify-content-center

          .row.mb-5.mb-md-4.mb-lg-0
            .col-lg-1.col-img.d-none.d-lg-flex
              img(src='@/assets/curso/temas/t1/lineaTiempo.svg', alt='')
              .linea_dots
            .col-lg-8.pt-md-4.pb-lg-4
              h5 Primera Etapa- Dcada de los 60
              p Joseph Carl Robnett Licklider informtico estadunidense, conceptualiza por primera vez la idea y concepto de la computacin en la nube.  
            .col-lg-3.col-md-8.col-10
              img(src='@/assets/curso/temas/t1/lineaTiempo_img1.png', alt='')
          .row
            .col-lg-1.col-img.d-none.d-lg-flex
              img(src='@/assets/curso/temas/t1/lineaTiempo.svg', alt='')
              .linea_dots
            .col-lg-11
              .row.pt-md-4
                .col-lg-9
                  h5 Segunda Etapa- Inicia sobre los 90 y va hasta 2006
                  p Empieza y se desarrolla el proceso de masificacin del acceso a internet, pasando a ser una tecnologa exclusiva, costosa y de acceso limitado para pocos, posteriormente se populariz, facilitando el acceso cada vez, a ms empresas, este mecanismo permiti que a travs de internet se originara lo que hoy conocemos con el trmino de aplicaciones como servicio.
                  .cajon.bg9.p-4.mb-4
                    p.mb-0 Dentro de esta etapa podemos destacar como uno de los mayores avances computacionales cuando se afianz el concepto de virtualizacin a finales de esa dcada, nace Salesforce empresa que desarrolla la primera ola de nube cuando propone ofrecer aplicaciones empresariales a travs de un acceso #[em web] sencillo y simple.
                .col-lg-3.col-md-8.col-10.mb-3.mb-lg-0
                  img(src='@/assets/curso/temas/t1/lineaTiempo_img2.png', alt='')
              p.pb-4 Si bien en esta etapa no se haba consolidado la computacin en la nube, la infraestructura, tecnologas y desarrollo tecnolgico propio del internet fue fundamental para el crecimiento y evolucin a lo que hoy conocemos como computacin en la nube.              
          .row.mb-4
            .col-lg-1.col-img.d-none.d-lg-flex
              img(src='@/assets/curso/temas/t1/lineaTiempo.svg', alt='')
              .linea_dots
            .col-lg-11.pt-md-4
              h5 Tercera Etapa  Reconocida histricamente en el 2007
              p Se desarrolla la tecnologa de poner lnea (internet) en toda la plataforma e infraestructura tecnolgica requerida por una empresa, incluyendo #[em hardware], #[em software] e #[em internetworking].
              .row
                .col-lg-9
                  .cajon.bg9.p-4.mb-4
                    p.mb-0 Nacen las grandes empresas como Google,  Amazon AWS, Apple Cloud, Microsoft Azure, Alibaba Cloud, que trabajan a partir del uso de internet y de servidores centralizados, con facilidades de acceso a servicios e infraestructura de manera remota, descentralizada y por demanda.
                  p La computacin en la nube en la ltima dcada pas de ser el servicio que grandes empresas de tecnologas ofrecan como una alternativa tecnolgica a sus clientes, para convertirse en un factor preponderante en la transformacin digital y adopcin de buenas prcticas que toda empresa necesita, la cuarta revolucin industrial, la popularizacin y cada vez ms necesaria adopcin de tecnologas como IoT, IA, #[em Big Data], #[em Machine Learning] entre otros hace necesario que se migre y se adopten estos servicios en la nube, ya no solo desde los servicios tradicionales si no que a medida que se van adoptando estas tecnologas irn surgiendo y desarrollando una nueva oferta de servicios que transforman y mejoran los procesos de negocio.
